$border-color:rgb(232, 232, 232);

body {
  background-color: #F8F9FF;
}

.ant-tabs-bar {
  margin: 0 !important;
}

.ant-tabs-top-content {
  border: 1px solid $border-color;
  border-top: none;
}


.ant-tabs-tab:hover {
  color: #675CFF !important;
}

.ant-tabs-tab-active {
  color: #675CFF !important;
}

.noborder {
  border-right: none !important;
}


.App {
  width: 1200px;
  margin: 0 auto;
  // background-color: palegoldenrod;
  min-height: 600px;
  display: flex;
  margin-top: 50px;

  .app-left {
    width: 180px;
    background-color: #fff;
    margin-right: 20px;
    box-shadow: 0px 0px 35px 0px rgba(94, 85, 126, 0.15);
    border-radius: 4px;
    padding-top: 20px;

    .platform-name {
      display: block;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      text-align: center;
      color: rgba(103, 92, 255, 1);

      border-right: 3px solid #675cff;
      margin-bottom: 15px;
    }
  }

  .app-right {
    flex: 1;
    background-color: #fff;
    box-shadow: 0px 0px 35px 0px rgba(94, 85, 126, 0.15);
    border-radius: 4px;
    padding-left: 20px;
    padding-right: 20px;

    .dispatcher {
      display: flex;
      width: 500px;
      justify-content: space-between;
      -moz-user-select: none;
      /*火狐*/
      -webkit-user-select: none;
      /*webkit浏览器*/
      -ms-user-select: none;
      /*IE10*/
      -khtml-user-select: none;
      /*早期浏览器*/
      user-select: none;

      .dispatcher-left {
        width: 200px;
        font-size: 22px;
        font-weight: bold;
        color: rgba(37, 33, 82, 1);
        padding-top: 12px;
        padding-bottom: 14px;
        // background-color: red;
        position: relative;
      }

      .dispatcher-right {
        flex: 1;
        // background-color: green;
        text-align: right;
        position: relative;
        padding-top: 17px;

        .dispatcher-right-edit {
          display: inline-block;
          position: relative;
          // background-color: red;
          width: 16px;
          height: 16px;

          img {
            width: 16px;
            position: absolute;
            left: -10px;
            top: 1px;
          }
        }

        .dispatcher-right-account {
          font-size: 16px;
          font-weight: 400;
          font-style: italic;
          color: rgba(103, 92, 255, 1);
          margin-right: 8px;
        }

        .dispatcher-right-text {
          font-size: 16px;
          font-weight: 400;
          color: #252152;
        }

        .dispatcher-right-balance {
          position: absolute;
          right: 0;
          bottom: -12px;
          font-size: 18px;
          font-weight: 500;
          color: rgba(37, 33, 82, 1);
        }
      }
    }

    .content-wrap {
      display: flex;

      .dispatcher-content {
        width: 500px;
        margin-right: 20px;

        .content-top {
          display: flex;
          border-bottom: 1px solid $border-color;
          // background-color: red;
          padding: 20px;

          .content-top-left {
            // background-color: palegoldenrod;
            flex: 1;
            position: relative;

            img {
              width: 46px;
              position: absolute;
              top: 0;
              left: 0;
            }

            .token-name {
              display: block;
              padding-left: 55px;
              line-height: 46px;
              font-size: 20px;
              font-weight: 500;
              color: rgba(37, 33, 82, 1);
            }

            .dispatcher-address {
              font-size: 14px;
              font-weight: 400;
              color: rgba(37, 33, 82, 1);
              padding-top: 15px;

              span {
                color: #675CFF;

                &:hover {
                  cursor: pointer;
                }
              }
            }
          }

          .content-top-right {
            // background-color: hotpink;
            width: 180px;
            text-align: right;

            .gross-title {
              display: block;
              font-size: 14px;
              font-weight: 400;
              color: rgba(164, 167, 190, 1);
            }

            .gross-num {
              font-size: 18px;
              font-weight: 500;
              color: rgba(37, 33, 82, 1);
            }
          }
        }

        .content-center {
          display: flex;
          border-bottom: 1px solid $border-color;
          // background-color: red;
          padding: 20px;
          padding-bottom: 5px;

          .content-center-left {
            flex: 1;
            // background-color: papayawhip;
          }

          .content-center-right {
            flex: 1;
            // background-color: yellow;
            text-align: right;
          }

          .item {
            height: 60px;
            // background-color: orange;
            // margin-bottom: 10px;

            .item-title {
              font-size: 14px;
              font-weight: 400;
              color: rgba(164, 167, 190, 1);

              .img-wrap {
                position: relative;

                img {
                  width: 16px;
                  position: absolute;
                  left: 5px;
                  top: 1px;
                }
              }
            }

            .item-num {
              font-size: 18px;
              font-weight: 500;
              color: rgba(37, 33, 82, 1);
            }
          }

        }

        .content-bottom {

          .content-bottom-top {
            display: flex;
            border-bottom: 1px solid $border-color;
            // background-color: red;
            height: 45px;
            padding-top: 10px;

            .btn-wrap {
              flex: 1;
              height: 25px;
              text-align: center;
              // background-color: peachpuff;
              display: flex;
              justify-content: center;
              align-items: center;
              border-right: 1px solid $border-color;

              .btn {
                // background-color: lawngreen;
                font-size: 16px;
                font-weight: 500;
                color: rgba(37, 33, 82, 1);
                cursor: pointer;
              }
            }
          }

          .content-bottom-bottom {
            display: flex;
            height: 45px;
            justify-content: center;
            align-items: center;

            .btn {
              // background-color: lawngreen;
              font-size: 16px;
              font-weight: 500;
              color: rgba(37, 33, 82, 1);
              cursor: pointer;
            }
          }
        }
      }

      .content-right {
        // background-color: red;
        flex: 1;

        .handler {
          font-size: 18px;
          font-weight: bold;
          color: #252152;
          height: 39px;
          display: block;
          // background: red;
          line-height: 39px;
        }

        .handler-item {
          border: 1px solid $border-color;
          margin-bottom: 20px;

          .handler-title {
            height: 46px;
            line-height: 46px;
            text-align: center;
            position: relative;
            background: rgba(243, 244, 251, 1);
            font-size: 18px;
            font-weight: 500;
            color: rgba(37, 33, 82, 1);

            .btn-clear {
              position: absolute;
              right: 15px;
              color: #675CFF;
              cursor: pointer;
            }
          }

          .item-wrap {
            display: flex;
            padding: 20px;
            padding-bottom: 5px;

            .handler-item-left {
              flex: 1;
            }

            .handler-item-right {
              flex: 1;
              text-align: right;
            }

            .item {
              height: 60px;

              .item-title {
                display: block;
                font-size: 14px;
                font-weight: 400;
                color: #a4a7be;
              }

              .item-num {
                display: block;
                font-size: 18px;
                font-weight: 500;
                color: #252152;
              }
            }
          }
        }
      }
    }



  }
}




.App-logo {
  height: 40vmin;
  pointer-events: none;
}



// useless
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}